.glass-panel {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.08);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
}
.upload-zone {
  position: relative;
  min-height: 300px;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.3s;
  border: 2px dashed var(--border);
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(8px);
}
@media (min-width: 768px) {
  .upload-zone {
    min-height: 400px;
  }
}
.upload-zone:hover {
  border-color: var(--primary);
  box-shadow: 0 0 30px rgba(117, 206, 234, 0.3);
}
.combat-button {
  position: relative;
  overflow: hidden;
  background: linear-gradient(135deg, var(--primary), var(--accent));
  animation: pulse-glow 2s ease-in-out infinite;
}
@keyframes pulse-glow {
  0%,
  100% {
    box-shadow: 0 0 20px rgba(117, 206, 234, 0.5);
  }
  50% {
    box-shadow: 0 0 40px rgba(117, 206, 234, 0.8);
  }
}
.subject-frame {
  position: relative;
  border-radius: 0.5rem;
  overflow: hidden;
  border: 2px solid;
  transition: all 0.5s;
  background: rgba(255, 255, 255, 0.02);
  backdrop-filter: blur(8px);
}
.subject-frame.winner {
  border-color: var(--primary);
  box-shadow: 0 0 40px rgba(117, 206, 234, 0.6);
  animation: winner-pulse 1.5s ease-in-out infinite;
}
.subject-frame.loser {
  border-color: rgba(239, 68, 68, 0.4);
  opacity: 0.7;
}
@keyframes winner-pulse {
  0%,
  100% {
    box-shadow: 0 0 40px rgba(117, 206, 234, 0.6);
  }
  50% {
    box-shadow: 0 0 60px rgba(117, 206, 234, 0.9);
  }
}
@keyframes scan {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(100%);
  }
}
.animate-scan {
  animation: scan 2s linear infinite;
}
@keyframes progress {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}
.animate-progress {
  animation: progress 3s ease-out;
}
.terminal-scroll {
  scrollbar-width: thin;
  scrollbar-color: var(--primary) transparent;
}
.terminal-scroll::-webkit-scrollbar {
  width: 6px;
}
.terminal-scroll::-webkit-scrollbar-track {
  background: transparent;
}
.terminal-scroll::-webkit-scrollbar-thumb {
  background-color: var(--primary);
  border-radius: 3px;
}
.delay-100 {
  animation-delay: 0.1s;
}
.delay-200 {
  animation-delay: 0.2s;
}
